 import {
     activeGood
 } from './updateGoodInfo-items'

 function mapVartualCard(list, id) {
     let len = list.length;
     let tmp = '';
     for (let i = 0; i < len; i++) {
         tmp = list[i];
         if (tmp.id == id) {
             return tmp;
         }
     }
     return {}
 }
 export function goodData(data) { //
     let static_resources_domain = this.Cacher.getData('static_resources_domain');
     let reg = new RegExp(static_resources_domain, 'g');
     let typeList = ['', '', '', '', '']; //
     let goodCateList = data.cate_list.map(item => { //
         return {
             name: item.title,
             id: item.id,
             children: item.children || []
         }
     }); //
     let cardSocks = data.virtual_list || []; //
     let virtual_card_id = data.goods.virtual_card_id || '';
     let cardType = {};
     let cardStocksLen = cardSocks.length;
     let tmpCard = {};
     for (let i = 0; i < cardStocksLen; i++) {
         tmpCard = cardSocks[i];
         if (tmpCard.id == virtual_card_id) {
             cardType = tmpCard;
             break;
         }
     }
     let tmparr = data.goods.category_ids;
     let allTypes = []; //
     data.cate_list.forEach(item => {
         allTypes.push({
             name: item.title,
             id: item.id,
             children: item.children || []
         });
         if (item.children && item.children.length) {
             item.children.forEach(val => {
                 allTypes.push({
                     name: val.title,
                     id: val.id,
                     children: val.children || []
                 });
             })
         }

     }); //
     let goodTypes = allTypes.filter(item => {
         let len = tmparr.length;
         let result = false;
         for (let i = 0; i < len; i++) {
             if (item.id == tmparr[i]) {
                 result = true;
                 break;
             }
         }

         return result;
     }); //
     let formList = data.form_list;
     formList.unshift({
         id: 0,
         name: ''
     })
     let statusList = ['', '', '', '', ''];

     let info = {
         info1: {
             goodType: {
                 label: '',
                 id: data.goods.id,
                 type: data.goods.type, ////1 2 3  4 5
                 value: typeList[data.goods.type - 1],
                 disabled: true, //,falsetrue
                 editable: 'input', //inputswitchimageimagelistselect
             },
             goodName: {
                 label: '',
                 id: data.goods.id,
                 value: data.goods.title,
                 disabled: false, //
                 editable: 'select', //inputswitchimageimagelistselect
             },
             subTitle: {
                 label: '',
                 id: data.goods.id,
                 value: data.goods.sub_title,
                 disabled: false, //
                 editable: 'select', //inputswitchimageimagelistselect
             },
             classification: {
                 label: '',
                 id: data.goods.id,
                 value: goodTypes.map(item => {
                     return item.name;
                 }).join(';'),
                 list: goodCateList, // 
                 category_ids: data.goods.category_ids,
                 goodTypes, //
                 disabled: false, //
                 editable: 'select', //inputswitchimageimagelistselect
             },
             mainImage: {
                 label: '',
                 id: '',
                 img: data.goods.thumb,
                 list: data.goods.thumb ? [{
                     img: data.goods.thumb.replace(reg, ''),
                 }] : [],
                 disabled: false, //
                 editable: 'image', //inputswitchimageimagelistselect
             },
             swiperList: {
                 label: '',
                 list: data.goods.thumbs.map(item => ({
                     img: item.replace(reg, '')
                 })),
                 disabled: false, //
                 editable: 'imagelist', //inputswitchimageimagelistselect
             }
         },
         info2: {
             specification: {
                 label: '',
                 id: data.goods.has_option == 0 ? 'single' : 'multi', //
                 value: data.goods.has_option == 0 ? "" : "",
                 list: (data.specs || []).map(item => {
                     item.disabled = !!data.goods.activity_goods
                     return item
                 }),
                 disabled: true, //
                 editable: 'input', //inputswitchimageimagelistselect 
                 type: 'edit'
             },
             childrenSpecs: { //
                 label: '',
                 id: '',
                 value: '',
                 formList: data.virtual_list,
                 list: (data.options || []).map(item => {
                     let other = {
                         display_order: item.display_order,
                         goods_id: item.goods_id,
                         sales: item.sales,
                         shop_id: item.shop_id,
                         stock_warning: item.stock_warning,
                         thumb: item.thumb,
                         virtual_card_id: item.virtual_card_id,
                         weight: item.weight,

                     }
                     return {
                         specif: {
                             label: '',
                             id: item.id,
                             value: item.title,
                             goodsId: item.goods_id,
                             disabled: true, //
                             editable: 'input', //inputswitchimageimagelistselect
                         },
                         price: {
                             label: '',
                             id: '',
                             value: item.price,
                             disabled: !!data.goods.activity_goods, //,
                             editable: 'input', //inputswitchimageimagelistselect
                         },
                         stock: {
                             label: '',
                             id: '',
                             value: item.stock,
                             disabled: !!data.goods.activity_goods, //,
                             editable: 'input', //inputswitchimageimagelistselect
                             needHide: data.goods.type == 3 //
                         },
                         cardStock: {
                             label: '',
                             id: item.virtual_card_id,
                             value: data.goods.type == 3 && mapVartualCard(data.virtual_list, item.virtual_card_id).name,
                             disabled: !!data.goods.activity_goods, //,
                             editable: 'input', //inputswitchimageimagelistselect
                             needHide: data.goods.type != 3
                         },
                         code: {
                             label: '',
                             id: '',
                             value: item.goods_code,
                             disabled: !!data.goods.activity_goods, //,
                             editable: 'select', //inputswitchimageimagelistselect
                         },
                         other,
                         specsId: item.specs,
                         disabled: !!data.goods.activity_goods, //

                     }
                 }),
                 disabled: false, //
                 editable: 'input', //inputswitchimageimagelistselect
             },
             price: { //
                 label: '',
                 id: '',
                 value: data.goods.price,
                 disabled: false, //
                 editable: 'input', //inputswitchimageimagelistselect
             },
             delPrice: { //
                 label: '',
                 id: '',
                 value: data.goods.original_price,
                 disabled: false, //
                 editable: 'input', //inputswitchimageimagelistselect
             },
             cardStock: { //
                 label: '',
                 id: virtual_card_id,
                 value: cardType.name,
                 formList: cardSocks,
                 needHide: data.goods.type != 3 || data.goods.has_option != 0, //
                 disabled: false, //
                 editable: 'select', //inputswitchimageimagelistselect
             },
             stockNum: { //
                 label: '',
                 id: '',
                 value: data.goods.stock,
                 disabled: false, //
                 editable: 'input', //inputswitchimageimagelistselect
             },
             showStock: { //
                 label: '',
                 id: '',
                 value: data.goods.stock_hide == 0,
                 disabled: false, //
                 editable: 'switch', //inputswitchimageimagelistselect
             },
             soldNum: { //
                 label: '',
                 id: '',
                 value: data.goods.sales_count,
                 disabled: false, //
                 editable: 'input', //inputswitchimageimagelistselect
             },
             showSold: { //
                 label: '',
                 id: '',
                 value: data.goods.sales_hide == 0,
                 disabled: false, //
                 editable: 'switch', //inputswitchimageimagelistselect
             }
         },
         info3: {
             goodCode: {
                 label: '',
                 id: '',
                 value: data.goods.goods_code,
                 disabled: false, //
                 editable: 'select', //inputswitchimageimagelistselect
             },
             autoExt: {
                 label: '',
                 id: '',
                 value: data.goods.auto_warehouse == 1,
                 disabled: false, //
                 editable: 'select', //inputswitchimageimagelistselect
                 needHide: data.goods.type != 3, ///3
             },
             autoExtTime: {
                 label: '',
                 id: '',
                 value: data.goods.auto_warehouse_time,
                 disabled: false, //
                 editable: 'select', //inputswitchimageimagelistselect
                 needHide: data.goods.type != 3, //3,
             },
             autoDeliver: {
                 label: '',
                 id: '',
                 value: data.goods.auto_delivery == 1,
                 disabled: false, //
                 editable: 'select', //inputswitchimageimagelistselect
                 needHide: data.goods.type != 2, //
             },
             autoDeliverContent: {
                 label: '',
                 id: '',
                 value: data.goods.auto_delivery_content,
                 disabled: false, //
                 editable: 'select', //inputswitchimageimagelistselect
                 needHide: data.goods.type != 2, //
             },
             provideCost: {
                 label: '',
                 id: '',
                 value: data.goods.dispatch_price,
                 dispatch_list: data.dispatch_list,
                 dispatch_id: data.goods.dispatch_id || 0,
                 dispatch_name: data.dispatch_list.filter(item => (item.id == data.goods.dispatch_id)).map(item => item.name)[0] || '',
                 disabled: false, //
                 editable: 'select', //inputswitchimageimagelistselect
                 needHide: data.goods.type == 2, //
             },
             showProCost: {
                 label: '',
                 id: '',
                 value: data.goods.dispatch_hide == 0,
                 disabled: false, //
                 editable: 'switch', //inputswitchimageimagelistselect
                 needHide: data.goods.type == 2, //
             },
             joinCount: {
                 label: '',
                 id: '',
                 value: data.goods.is_discount == 1,
                 disabled: false, //
                 editable: 'switch', //inputswitchimageimagelistselect
             },
             goodForm: {
                 label: '',
                 id: data.goods.form_id,
                 value: formList.filter(item => {
                     return item.id == data.goods.form_id
                 }).map(item => {
                     return item.name
                 }).join(''),
                 formList,
                 disabled: false, //
                 editable: 'select', //inputswitchimageimagelistselect
             }
         },
         info4: {
             status: {
                 label: '',
                 id: data.goods.status,
                 value: statusList[data.goods.status * 1 + 1],
                 putaway_time: data.goods.putaway_time,
                 disabled: false, //
                 editable: 'select', //inputswitchimageimagelistselect
             },
             sale: {
                 label: '',
                 id: '',
                 value: data.goods.sales_count,
                 goodsId: data.goods.id,
                 disabled: false, //
                 editable: 'select', //inputswitchimageimagelistselect
                 needHide: false
             }
         },
     }
     return info;
 }
 export function addGoodsModel(data) {
     let dataModel = goodData.call(this, data);
     dataModel.info1.goodType.disabled = false;
     dataModel.info2.specification.disabled = false;
     dataModel.info2.specification.type = 'add';
     dataModel.info4.sale.needHide = true;
     return dataModel;


 }