export function activeGood(data, state) { // 
    function searchInput(obj) {
        if (typeof obj == 'object') {
            for (let k in obj) {
                if (obj[k].editable) {
                    obj[k].disabled = true;
                }  else {
                    searchInput(obj[k])
                }
            }
        }

    }
    if (state) {
        searchInput(data)
    }
    
    return data
}