export default function (that, downloadUrl) { 

    function downloadFile() {

        if (/^http.+/.test(downloadUrl)) {//
            uni.downloadFile({
                url: downloadUrl,
                success: function (res) {
                    //  
                    uni.saveImageToPhotosAlbum({
                        filePath: res.tempFilePath,
                        success(res) {
                            that.Toast('');
                        },
                        fail(res) {
                            that.Toast('');
                        }
                    })
                },
                fail: function (res) {
                    that.Toast("");
                }
            })
        } else {//
            //   
            uni.saveImageToPhotosAlbum({
                filePath: downloadUrl,
                success(res) {
                    that.Toast('');
                },
                fail(res) {
                    that.Toast('');
                }
            })
        }



    }

    if (!uni.saveImageToPhotosAlbum) {
        that.Dialog.alert({
            title: '',
            message: ''
        }).then(() => {
            // on close
        });
        return;
    }

    //  wx.getSetting  "scope.writePhotosAlbum"  scope 
    uni.getSetting({
        success(res) {
            if (!res.authSetting['scope.writePhotosAlbum']) {
                //  
                uni.authorize({
                    scope: 'scope.writePhotosAlbum',
                    success() {
                        downloadFile.call(that, downloadUrl);
                    },
                    fail() {
                        //  
                        //  
                        uni.openSetting({
                            success: function (data) {},
                            fail: function (data) {}
                        });
                    }
                })
            } else {
                downloadFile.call(that, downloadUrl);

            }
        },
        fail(res) {
            that.Toast("")
        }

    })

}